.alerta {
  margin-top: 1rem;
  padding: 0.5rem;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  border: 1px solid transparent;
  border-radius: 4px;
}
.alerta.error {
  background-color: #f2dede;
  color: #a94442;
  border-color: #ebccd1;
}
.alerta.exito {
  background-color: #dff0d8;
  color: #3c763d;
  border-color: #d6e9c6;
}

table.propiedades {
  width: 100%;
  border-collapse: collapse;
  margin-top: 2rem;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #f8f8f8;
}
table.propiedades th, table.propiedades td {
  padding: 1rem;
  text-align: left;
  border-bottom: 1px solid #ddd;
}
table.propiedades .imagen-tabla {
  width: 20rem;
  height: auto;
}

.imagen-small {
  display: block;
  width: 40rem;
  height: auto;
  margin: 1rem auto;
}